.WorldWideCovidBoard{
    max-width: 1024px;
    margin: 0 auto;
    padding: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 1rem;
}

.WorldWideCovidBoard .label{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-group{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
}

.main-group .group.n1{
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
}

.main-group .group.n1 span{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1rem;
}

.main-group .group.n1 span .icon{
    width: 3rem;
    height: 3rem;
}

.main-group .group.n2 span .icon{
    width: 1.5rem;
    height: 1.5rem;
}

.main-group .group span .text{
    display: flex;
    flex-direction: column;
}

.main-group .group .span-inline{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-group .group.n2{
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
}

.main-group .group.n2 .label{
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.main-group .group.n2 span{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
}
