.Navbar{
    border-bottom: 1px solid black;
}

.nav{
    padding: 1.2rem;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
}

.nav h2{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nav i{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
}